.footer-container {
  background-color: #000536;
  padding: 2rem 0 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-subscription {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 12px;
  padding: 12px;
  color: #fff;
}

.footer-subscription > p {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.footer-subscription-heading {
  margin-bottom: 12px;
  font-size: 24px;
}

.footer-subscription-text {
  margin-bottom: 12px;
  font-size: 20px;
}

.footer-input {
  padding: 8px 20px;
  border-radius: 2px;
  margin-right: 10px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}

.footer-links {
  width: 100%;
  max-width: 1000px;
  /* display: contents; */
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  /* margin: 16px; */
  text-align: center;
  box-sizing: border-box;
}

.footer-link-item {
  margin: 5px;
  padding: 5px;
  color: #fff;
  text-decoration: none;
}

.footer-link-items h2 {
  margin-bottom: 16px;
}

.footer-link-items > h2 {
  color: #fff;
}

.footer-link-items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
  color: #808080;
  transition: 0.3s ease-out;
}

.footer-email-form h2 {
  margin-bottom: 2rem;
}

.footer-input::placeholder {
  color: #b1b1b1;
}

/* Social Icons */

.social-icon-link {
  color: #fff;
  font-size: 24px;
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
}

.social-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.website-rights {
  color: #fff;
  margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
  .footer-container {
    padding: 1rem 0 1rem 0;
  }
  .footer-links {
    padding-top: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .social-media-wrap {
    flex-direction: column;
  }
}

@media screen and (max-width: 768px) {
  .footer-container {
    padding: 1rem 0 1rem 0;
  }

  .footer-subscription {
    padding: 7px;
  }

  .footer-subscription-heading {
    margin-bottom: 10px;
    font-size: 18px;
  }

  .footer-subscription-text {
    margin-bottom: 10px;
    font-size: 14px;
  }

  .footer-input {
    padding: 4px 10px;
    border-radius: 2px;
    font-size: 14px;
    margin-bottom: 10px;
  }

  .footer-links {
    display: grid;
    grid-template-columns: 100px 100px;
    padding: 0px;
  }
}
